import { Container } from "react-bootstrap";
import CharacterDatabase from "../Character/CharacterDatabase";
import CharacterDisplayCard from "../Character/CharacterDisplayCard";
import { DatabaseInitAndVerify } from '../DatabaseUtil';
export default function TestDisplay(props) {
  DatabaseInitAndVerify()
  let character = CharacterDatabase.getCharacter(CharacterDatabase.getCharacterIdList()[0])
  console.log("loaded from db", character)
  return <Container>
    <CharacterDisplayCard editable characterToEdit={character} />
  </Container>
}